.semiFooter-container{
    background: #F1F9FF;
    width: 85%;
    min-height: 300px;
    margin: auto;
    display: flex;
    gap:  0 10px;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-items: center;
    margin-bottom: 20px;
    overflow: hidden;

}
.semiFooter-container  ul li a{
    color: var(--main-color);
    font-size: 36px;
    font-weight: 600;
    line-height: 54px;
    margin: 50px 0;
    display: flex;
    gap: 20px;
}
.semiFooter-container .semiFooterIcon{
    font-size: 50px;
}