.market-container{
    min-height: 100vh;
    padding: 30px;
}
.market-container .relative-links{
    width: 85%;
    margin-left: auto;
    margin-right: auto;
}
.market-cards{
    margin: auto;
    display: flex;
    gap:50px 30px;
    justify-content: space-between;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    padding: 5%;
}
.market-cards .card{
    background: #D9D9D9;
    width:calc(75%/3);
    min-width: 300px;
    display: flex;
    gap: 10px;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;
    padding-bottom: 50px;
    border-radius: 43px;
}
.market-cards .card img{
    width: 300px;
}
.market-cards .card h3{
    color: var(--main-color);
}
.market-cards .card p{
    color: var(--main-color);
    font-size: 24px;
    text-align: center;
}
.market-cards .card button{
    color: white;
    font-size: 40px;
    font-weight: 700;
    line-height: 60px;
    width: 100%;
    padding:9px 0;
    border-radius: var(--main-border-radious);
    border-radius: 39px;
    position: absolute;
    bottom: -34px;
    left: 50%;
    transform: translate(-50%);
}
