.productFilter-container{
    z-index: 999999 !important;
    padding: 30px;
    background-color: var(--third-color);
    min-height: 234px;
    width: 85%;
    margin:50px auto auto auto;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-evenly;
}
.productFilter-container >div{
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: calc(95%/3);
    min-width: 150px;
}
.productFilter-container >div h4{
    color: var(--main-color);
    font-size: 36px;
    line-height: 54px;
}
.productFilter-container >div select{
    position: relative;
    color: var(--six-color);
    width:100%;
    height: 59px;
    outline: 0;
    border: 0;
    padding: 10px;
    font-size: 24px;
    font-weight: 500;
    line-height: 36px;
    /* text-align: center; */
    /* Remove default arrow in Firefox */
    -moz-appearance: none; 
    /* Remove default arrow in other browsers */
    appearance: none; 
}
.productFilter-container >div select option{
    color: black;
}
.faAnglesUp{
    position: absolute;
    right: 0;
    bottom: 0;
    right: 20px;
    transform: translateY(calc(-50% - 5px));
    font-size: 28px;
    color: var(--main-color);
    color: var(--six-color);
}