.navbar-container{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    border-top: 32px solid var(--main-color);
    padding: 10px;
    position: relative;
}
.navbar-container .logo img{
    width: 178px;
    height: 68px;
}
.navbar-container ul{
    display: flex;
    justify-content: space-between;
    list-style-type: none;
    gap: 20px;
}
.navbar-container ul li a{
    color: var(--main-color);
    text-decoration: none;
    text-transform: capitalize;
    font-size: var(--font-size);
    font-weight: 500;
    line-height: 42px;
}
.navbar-container ul li a.active{
    color: var(--second-color);
}
.navbar-container .nav-btn .navSearchIcon{
    color: var(--main-color);
    font-size: 25px;
    cursor: pointer;
    margin-right: 10px;
}
.navbar-container div button{
    background-color: var(--main-color);
    color: white;
    padding: 5px 40px;
    border-radius: 39px;
    font-size: var(--font-size);
    font-weight: 700;
    line-height: 36px;
}
/* popup */
.navbar-container .searchPopup{
    background-color: white;
    height: 80px;
    width: 300px;
    padding:10px 5px;
    border-radius: 5px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 10px;
    position: absolute;
    bottom: -70px;
    right:100px;
    box-shadow: 4px 10px 10px 4px #00000040;
    z-index: 9999999;
}
.navbar-container .searchPopup .popupExit{
    cursor: pointer;
    color: var(--main-color);
    align-self: flex-end;
}
.navbar-container .searchPopup .searchInputContainer{
    position: relative;
}
.navbar-container .searchPopup .searchInput{
    background-color: var(--third-color);
    background-color: white;
    color: var(--fifth-color);
    height: 40px;
    width: 100%;
    padding: 10px;
    border-radius: 3px;
    margin: auto;
    border: 1px solid var(--main-color);
    position: relative;
    font-size: larger;
    text-transform: capitalize;
}
.navbar-container .searchPopup .popupSearchIconContainer{
    position: absolute;
    right:0px;
    top: 0;
}
.navbar-container .searchPopup .popupSearchIcon{
    background-color: var(--main-color);
    color: white;
    padding:7px 2px;
    font-size: 25px !important;
}
/* end of popup */
.navbar-container .bars{
    display: none;
    font-size: 48px;
    color: var(--main-color);
}
.navbar-container .sideBar{
    padding: 30px;
    position: absolute;
    background-color: rgba(255, 255, 255, 0.9);
    width: 300px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    top: 0;
    right: 0;
    height: 100vh;
    z-index: 999999999999999;
    animation-name: sideBar;
    animation-duration: var(--transition-duration);
    overflow: hidden;
}
@keyframes sideBar {
    from{
        /* margin-left: -1000px; */
        transform: translateX(1000px);

    }
    to{
        /* margin-left: 0; */
        transform: translateX(0);

    }
}